<template>
	<div>
		<div class="swiper-container">
		    <div class="swiper-wrapper">
		        <div class="swiper-slide"  v-for="item in news">
		        	<img :src="item.thumb" />
		        </div>
		    </div>
		    <!-- 如果需要分页器 -->
		    <div class="swiper-pagination"></div>
		</div>
		<div  class="lg">
			<ul  
				v-infinite-scroll="loadMore"  
  				infinite-scroll-disabled="loading"
 				infinite-scroll-distance="10"><!--1  10以内触发   无限滚动是否关闭，true不可以发送请求-->
				<li  v-for="item in list"  class="row">
					<div  class="left">
						<img  :src="'http://www.lgstatic.com/' + item.companyLogo"  width="60"  height="60"/>
					</div>
					<div  class="right">
						<h3  class="title">{{item.companyFullName}}</h3>
						<span  class="position">{{item.positionName}}</span>
						<p  class="createTime">{{item.createTime}}</p>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import {mapActions,mapMutations,mapState}  from 'vuex'/*名字不可以随意变化*/
	import { Toast } from 'mint-ui';
	import axios from 'axios';
	import '../../../node_modules/swiper/dist/css/swiper.css';
	import Swiper from 'swiper';
	import '../../../node_modules/swiper/dist/js/swiper.js';
	export default{
		data(){
			return{
				news : []
			}
		},
		created(){
			axios.get("/sina/api/v3/get?_=1543577068041").then((res)=>{
				this.news = res.data.data.filter((item,index)=>{
					return index < 5
				})
			});
			/*this.$jsonp("/wy/special/00804KV1/post1603_api_all.js?_=154363687697",{
				callbackName : 'callback'
			}).then((res)=>{
				this.list = res.data.filter((item,index)=>{
					return index < 10  && item.pic[0]
				})
			})*/
			this.getData();
		},
		computed:{
			...mapState(["list"])//...mapState(["模块名"，"数据"])数据用模块名.数据  进行调用
		},
		methods : {
			loadMore(){
				this.getData();
				/*console.log(111)*/
			},
			...mapActions(["getData"])//...mapActions(["函数名"])
		},
		watch:{
			news(){
				this.$nextTick(()=>{
					var mySwiper = new Swiper ('.swiper-container', {
					    loop: true, // 循环模式选项
					    autoplay:true,
					    // 如果需要分页器
					    pagination: {
					      el: '.swiper-pagination',
					    },
					    autoplay: {
						    delay: 3000,
						    disableOnInteraction: false,//用户操作后还能自动播放
						    },
					  })        
				})
			}
		}
	}
</script>
<style  lang="scss">
	$sc:25;
	.swiper-container{
		width: 100%;
		height: 134/$sc+rem;
		.swiper-wrapper{
			width: 100%;
			height: 100%;
			img{
				width: 100%;
				height: 100%;
			}
		}
	}
	.row{
		display: flex;
		li{
			width: 347/$sc+rem;
			padding: 14/$sc+rem;
			height: 62/$sc+rem;
			.right .title{
				width: 347221.594/$sc+rem;
				height: 23/$sc+rem;
				font-size: 1.6rem;
				font-weight: normal;
			}
		}
	}
	.lg ul li{
		width: 100%;
		height: 62/$sc+rem;
		padding: 14/$sc+rem;
		.left img{
			width: 60/$sc+rem;
			height: 60/$sc+rem;
		}
		.right{
			padding-left: 10/$sc+rem;
			width: 277/$sc+rem;
			height: 62/$sc+rem;
			.title{
				font-size: 16px;
			}
			.position{
				font-size: 12px;
				margin-bottom: 6/$sc+rem;
			}
			.createTime{
				color: #888;
				font-size: 8px;
				padding-top: 6/$sc+rem;
			}
		}
	}
</style>



