import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import { Toast } from 'mint-ui';
import axios from 'axios';
var store = new Vuex.Store({
	state : {
		list : [],
		page :2,//页面下标
		loading : false//可以发送请求，true不可以发请求
	},
	mutations : {
		moreList(state,newList){
			state.list = state.list.concat(newList)
		},
		changePage(state,p){
			state.page++
		},
		changeLoading(state,l){
			state.loading = l
		}
	},
	actions : {
		getData(context){
				if(context.state.page === 10){
					Toast({
					  message: '没有而更多数据了',
					  position: 'middle',
					  duration: 2000,//-1时，不会自动关闭，当点击下一个按钮时才关闭，ajax发送数据时需要这个方法
					  //若需在文字上方显示一个图标图标，可以将图标的类名作为iconClass的值传给Toast
						
					});
					return 	
					};
					context.commit("changeLoading",true)
				/*this.loading = true;*///同一时间其他请求不能发
				var one = Toast({
				  message: 'upload  before',
				  position: 'middle',
				  duration: -1,//-1时，不会自动关闭，当点击下一个按钮时才关闭，ajax发送数据时需要这个方法
				  //若需在文字上方显示一个图标图标，可以将图标的类名作为iconClass的值传给Toast
				  iconClass:'fa fa-spinner  fa-pulse'
				});
				axios.get("/lg/listmore.json",{
					params : {
						pageNo : context.state.page,
						pageSize : 15
					}
				}).then((res)=>{
					console.log(res)
					context.commit("moreList",res.data.content.data.page.result);
					context.commit("changePage");
					context.commit("changeLoading",false);
					one.close()
				})
			}
	}
})

export default store



