<template>
	<div>
		<header>
			<a  href="#"  class="one">返回</a>
			<a class="two"   @click="go">注册</a>
		</header>
		<section>
			<div  class="pic">
				<img  src="../../../static/image/logo.png" />
			</div>
			<div  class="login">
				<label><input type="tel" placeholder="手机/邮箱"  v-model="user"/></label>
				<label><input type="password" placeholder="密码"  v-model="userpwd"/></label>
			</div>
		</section>
		<aside>
			<div  class="btn">
				<input @click="login" type="submit" value="登录"/>
			</div>
		</aside>
	</div>
</template>
<script>
import axios from 'axios';
	export default{
		data(){
			return{
				user:'',
				userpwd : '',
				list : []
			}
		},
		methods:{
			go(){
				this.$router.push("/register");
			},
			login(){
				axios.get("http://localhost:3000/list").then((res)=>{
					this.list = res.data;
					var total = this.list.filter((item)=>{
						return (item.tel === this.user || item.email === this.user) && item.password === this.userpwd
					});
					if(total.length > 0){
						this.$router.push('/home');
					}else{
						this.$router.push('/register');
					}
				})
			}
		}
	}
</script>
<style  lang="scss">
	@import url("../../style/reset.css");
	$sc : 25;
	header{
		width: 100%;
		height: 43.984/$sc+rem;
		background: #fff;
		.one{
			float:left;
			line-height:43.984/$sc+rem;
			margin-left: 10/$sc+rem;
		};
		.two{
			float:right;
			line-height:43.984/$sc+rem;
			margin-right: 10/$sc+rem;
		}
	}
	section{
		background: #f4f4f4;
		width: 100%;
		height: 324/$sc+rem;
		position: relative;
		.pic{
			width: 147.484/$sc+rem;
			height:  147.484/$sc+rem;
			position: absolute;
			top: 1.8rem;
			left:4rem;
			img{
				width: 100%;
				height: 100%;
			}
		}
		.login{
			width: 100%;
			height: 100/$sc+rem;
			position: absolute;
			bottom: 0;
			left: 0;
			label{
				display: block;
				width: 100%;
				height: 49.016/$sc+rem;
				border-top: 0.484/$sc+rem  solid #dbdbdb;
				border-bottom: 0.484/$sc+rem  solid #dbdbdb;
				input{
					width: 100%;
					height: 100%;
					outline: none;
					border: none;
				}
			}
		}
	}
	aside{
		width: 351/$sc+rem;
		height: 249/$sc+rem;
		margin: 0  auto;
		padding-top: 15/$sc+rem;
		background: #f4f4f4;
		.btn{
			width: 349/$sc+rem;
			height: 35.5/$sc+rem;
			input{
				width: 100%;
				height: 100%;
				background: #fff;
				outline: none;
				border: 1/$sc+rem solid  #dbdbdb;
			}
		}
	}
</style>


