<template>
	<div>
		<div  class="nav">
			<div class="leftNav">
				<div class="swiper-container2">
				    <div class="swiper-wrapper">
				        <div class="swiper-slide"  v-for="(item,index) in titles"  :class="{'active': curIndex===index}">
				        	{{item.name}}
				        </div>
				    </div>
				</div>
			</div>
			<div  class="fa fa-navicon"></div>
		</div>
		<router-view />
	</div>
</template>
<script>
	import axios from 'axios';
	import '../../../node_modules/swiper/dist/css/swiper.css';
	import Swiper from 'swiper';
	import '../../../node_modules/swiper/dist/js/swiper.js';
	export default{
		data(){
			return{
				curIndex : 0,
				titles:[
					{
						"name" : "头条",
						"type" : "pcent"
					},
					{
						"name" : "娱乐",
						"type" : "pcent"
					},
					{
						"name" : "科技",
						"type" : "pctech"
					},
					{
						"name" : "体育",
						"type" : "pcspt"
					},
					{
						"name" : "财经",
						"type" : "pcfin"
					},
					{
						"name" : "军事",
						"type" : "wmil"
					},
					{
						"name" : "音乐",
						"type" : "pcent"
					},
					{
						"name" : "热点",
						"type" : "pcent"
					},
					{
						"name" : "视频",
						"type" : "pcent"
					},
					{
						"name" : "奇趣",
						"type" : "pcent"
					},
					{
						"name" : "图片",
						"type" : "pcent"
					},
					{
						"name" : "电影",
						"type" : "pcent"
					},
					],
			
			}
		},
		created(){
			axios.get("/sina/api/v3/get?_=1543577068041").then((res)=>{
				/*console.log(res)*/
				this.news = res.data.data.filter((item,index)=>{
					return index < 5
				})
			})
			this.$jsonp("/wy/special/00804KV1/post1603_api_all.js?_=154363687697",{
				callbackName : 'callback'
			}).then((res)=>{
				/*console.log(res)*/
			})
		},
		mounted(){//如果数据是静态的，swiper可以在这里实例化，但如果是动态的
			 var vm = this;//this.curIndex = this.clickedIndex//错误，this指向swiper
			 //因此vm相当于vue的实例
			 var title = new Swiper ('.swiper-container2', {
			 	freeMode : true,//必须写
			 	slidesPerView : 'auto',//默认显示一个,必须写
			 	on:{
			 		click : function(){//单击，当前导航条到中心位置
			 			vm.curIndex = this.clickedIndex;
			 			this.setTransition(200);//this指向swipper
			 			var el = this.slides[this.clickedIndex];//当前点击元素以及下面是中心
			 			var elPos = el.offsetLeft + el.clientWidth / 2;
			 			var swiperContainerWidth = this.wrapperEl.clientWidth;//包含快的大小
			 			var maxScroll = this.maxTranslate();//最大的滚动距离
			 			var maxPos = -maxScroll + swiperContainerWidth/2;
			 			//console.log(elPos,swiperContainerWidth,maxScroll,maxPos);//最大的中心位置
			 			if(elPos < swiperContainerWidth / 2){
			 				this.setTranslate(0);
			 			}else  if(elPos > maxPos){
							this.setTranslate(maxScroll);
			 			}else{
			 				this.setTranslate(swiperContainerWidth / 2 - elPos);
			 			}
			 			/*alert(this.clickedIndex)*/
			 			if(vm.curIndex === 0){
			 				vm.$router.push({'name':'list'})
			 			}else{
			 				vm.$router.push("/home/index/" + vm.titles[vm.curIndex].type)//重点,不是this.type 
			 			}
			 			
			 		}
			 	}
			 })
		}
	}
</script>
<style  lang="scss">
	$sc:25;
	.nav{
		width: 375/$sc+rem;
		height: 37.5/$sc+rem;
		display: flex;
		justify-content: space-around;
		background: #fff;
		overflow: hidden;
		.leftNav{
			width: 90%;
			height: 100%;
			overflow: hidden;
			margin-left: -10/$sc+rem;
			.swiper-wrapper{
				width: 337/$sc+rem;
				height: 100%;
				.swiper-slide{
					width: 48.5/$sc+rem;
					height: 100%;
					line-height: 37.5/$sc+rem;
					text-align: center;
				}
				.active{
					color: #ff5700;
					border-bottom: 2px  solid #FF5700;
				}
			}
		}
		.fa-navicon{
			width: 10%;
			text-align: center;
			line-height: 37.5/$sc+rem;
		}
	}
</style>



