<template>
	<div  ref="list"  class="list"  v-scroll="visible"  dis="300"><!--v-自定义命令-->
		<div>
			<ul>
				<li  v-for="item in list"  class="row">
					<div>
						<img  :src="item.thumb" />
					</div>
				</li>
			</ul>
		</div>
		<button  id="btn"  v-show="visible.flag" v-top>返回顶部</button>
	</div>
</template>
<script>
	import axios from 'axios';
	import '../../../node_modules/swiper/dist/css/swiper.css';
	import Swiper from 'swiper';
	import '../../../node_modules/swiper/dist/js/swiper.js';
	export default{
		data(){
			return{
				list : [],
				type : '',
				visible : {
					flag : false
				}
			}
		},
		/*mounted(){
			console.log(this.$refs.list);
			//window.addEventListener("scroll",()=>{})失灵，因为是局部滚动
			this.$refs.list.addEventListener("scroll",()=>{//必须有高度，且overflow: scroll
				console.log(111)
			})
		},*/
		watch:{//因为需要监听路由的参数
			$route : {
				handler(n,o){
					this.type = n.params.type;
					this.getData();
				},
				immediate : true
			}
		},
		methods : {
			getData(){
				this.$jsonp(`/sina/api/v3/get?cateid=1&cre=tianyi&mod=${this.type}&merge=3&statics=1&length=20&up=1&down=0&fields=media&_=1543835667802`).then((res)=>{//res代表回调函数
				console.log(res)
				this.list = res.data.filter((item,index)=>{
					return index > 10
				})
				})
			}
		}
	}
</script>
<style  lang="scss">
	$sc:25;
	.swiper-container{
		width: 100%;
		height: 134/$sc+rem;
		.swiper-wrapper{
			width: 100%;
			height: 100%;
			img{
				width: 100%;
				height: 100%;
			}
		}
	}
	.list{
		position: absolute;
		left: 0;
		right: 0;
		top:37.5/$sc+rem; ;
		bottom: 0;
		overflow: scroll;
		#btn{
			position: fixed;
			bottom:100/$sc+rem;
			right: 50/$sc+rem;
			font-size: 20px;
		}
	}
	.row  img{
		width: 150/$sc+rem;
		height: 150/$sc+rem;
	}
</style>



