import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/page/home'
import Index from '@/components/page/index'
import List from '@/components/page/list'
import typenews from '@/components/page/typenews'
import Video from '@/components/page/video'
import Subscribe from '@/components/page/subscribe'
import User from '@/components/page/user'
import Login from '@/components/page/login'
import Register from '@/components/page/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:{'name':'home'}
    },
    {
      path: '/home',
      name: 'home',
      redirect:{'name':'index'},
      component: Home,
      children : [
      	{
      		  path: '/home/index',
      			name: 'index',
      			component: Index,
      			redirect : {'name' : 'list'},
      			children : [
      					{
      						path: '/home/index/list',
      						name: 'list',
      						component: List,
      					},
      					{
      						path: '/home/index/:type',
      						name: 'typenews',
      						component: typenews,
      					}
      				]
      	},
      	{
      		  path: '/home/video',
      			name: 'video',
      			component: Video,
      	},
      	{
      		  path: '/home/subscribe',
      			name: 'subscribe',
      			component: Subscribe,
      	}
      	]
    },
     {
      path: '/user',
      name: 'user',
      component: User,
      redirect:{'name' : 'login'},
      children : [
      		{
      			  path: '/login',
      				name: 'login',
      				component: Login,
      		},
      		{
      			  path: '/register',
      				name: 'register',
      				component: Register,
      		}
      		]
    }
  ]
})



